.select {
	margin-bottom: 1.5rem;
	> label {
		margin: 0 0 0.3rem 0;
		padding: 0;
		font-weight: bold;
		display: block;
	}
	> select {
		display: block;
		box-sizing: border-box;
		width: 100%;
		padding: 0.3rem;
		outline: none;
		background: initial;
		border-radius: 0.4em;
	}
}